.navbar
{
    display: flex;
    justify-content: space-between;
    height: 10vh;
    position: fixed;
    top: 0;
    left: 0;
    width: 98vw;
    z-index: 100;
   
}
.active {
    color: #ffffff;
    background-color: var(--blue);
}
.inactive {
    background-color: transparent;
}

a {
    color: #000;
}

.navbar__links
{
    display: flex;
    align-items: center;
}

.navbar__links > *
{
    text-decoration: none;
    padding: 0.5rem 1rem;
    border-radius: 15px;
    margin-left: 20px;
    text-transform: uppercase;
    font-weight: 600;
    position: relative;
    cursor: pointer;
    transition: all .5s ease-out;

}
